import React, { useState } from "react";
import toast from "react-hot-toast";
import { useLDClient } from "launchdarkly-react-client-sdk";
import Toasts from "./toaster";

export default function Login() {
  const LDClient = useLDClient();

  const [userState, setUserState] = useState({
    username: "",
  });

  async function setCurrLDUser() {
    const obj = await LDClient.getUser();
    return obj;
  }

  const submitUser = async (e) => {
    e.preventDefault();
    const lduser = await setCurrLDUser();
    lduser.key = userState.username;
    await LDClient.identify(lduser);
    LDClient.track("userLogin", { customProperty: userState.username });
    toast.success("Your LaunchDarkly user is " + userState.username);
    console.log("The updated user is: " + lduser.key);
    Array.from(document.querySelectorAll("input")).forEach(
      (input) => (input.value = "")
    );
  };

  const clearUser = async (e) => {
    e.preventDefault();
    const lduser = await setCurrLDUser();
    lduser.key = "anonymous";
    await LDClient.identify(lduser);
    LDClient.track("userClear", { customProperty: userState.username });
    await console.log(LDClient.getUser());
    toast.success("User has been cleared");
    Array.from(document.querySelectorAll("input")).forEach(
      (input) => (input.value = "")
    );
    await console.log(userState)
  };

  const handleChange = (e) => {
    const { id, value } = e.target;
    setUserState((prevState) => ({
      ...prevState,
      [id]: value,
    }));
  };

  return (
    <div className="">
      <Toasts />
      <div className={` p-4 px-7 2xl:px-14 w-full`}>
        <div className="text-5xl py-5 font-audimat">Login with your username</div>
        <form>
          <div className="flex overflow-hidden px-2 py-1 justify-between">
            <input
              className="text-2xl bg-ldinput border-lddblue border-0 border-b-4  text-gray-600 flex-grow px-2 box-content h-16 w-16"
              type="input"
              id="username"
              placeholder="Username"
              value={userState.username}
              onChange={handleChange}
            />
          </div>
          <div className="flex items-center pt-10 px-2 py-6 space-x-4 mx-auto submission">
            <button
              type="submit"
              id="submission"
              className="bg-lddblue text-white text-2xl px-6 py-4 w-full "
              onClick={submitUser.bind(userState)}
            >
              Submit
            </button>            
            <button
              type="submit"
              id="clear"
              className="bg-ldred text-white text-2xl px-6 py-4 w-full "
              onClick={clearUser.bind()}
            >
              Clear
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
